import { Injectable } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase/app';
import {
  AngularFirestore,
  AngularFirestoreCollection
} from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import { Test } from 'tslint/lib';
@Injectable({
  providedIn: 'root'
})
export class AuthService {
  TestRef: AngularFirestoreCollection<any>;
  user: any;
  constructor(private firebaseAuth: AngularFireAuth, private afs: AngularFirestore) {
    this.TestRef = this.afs.collection<any>('test');
this.firebaseAuth.authState.subscribe(user => {
this.user = user.uid;
console.log(user);
});
   }
  public login(d) {
    return  this.firebaseAuth
        .auth
        .signInAndRetrieveDataWithEmailAndPassword(d.value.email, d.value.password);

    }
    public add(d, id) {
     return this.TestRef.add({question: d.question, a: d.a ,  b: d.b, c: d.c, d: d.d,  answer: d.answer, lecturer_id:id});
    }


}
export interface Test {
question: string;
a: string;
b: string;
c: string;
d: string;
answer: string;

 }