import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup , Validators, AbstractControl} from '@angular/forms'
import { AuthService } from '../auth.service';

@Component({
  selector: 'app-ques',
  templateUrl: './ques.component.html',
  styleUrls: ['./ques.component.css']
})
export class QuesComponent implements OnInit {
question: AbstractControl;
a: AbstractControl;
b: AbstractControl;
c: AbstractControl;
d: AbstractControl;
formgroup: FormGroup ;
answer: AbstractControl;
sucess: any;
err: any;
  constructor(public formbuilder: FormBuilder, private service: AuthService) {
    this.formgroup = formbuilder.group({
     question: ['',  Validators.required],
     a: ['',  Validators.required],
      b: ['', Validators.required],
      c: ['',  Validators.required],
      d: ['', Validators.required],
      answer: ['', Validators.required]
    });
this.question = this.formgroup.controls['question'];
this.a = this.formgroup.controls['a'];
this.b = this.formgroup.controls['b'];
this.c = this.formgroup.controls['c'];
this.d = this.formgroup.controls['d'];
this.answer = this.formgroup.controls['answer'];
   }

  ngOnInit() {
    localStorage.setItem('uuid', this.service.user);
  }
  onSubmit(formgroup) {
    console.log(formgroup);
    const id = this.service.user;
    this.service.add(formgroup.value, localStorage.getItem('uid'))
    .then((data)  => {
      this.formgroup.reset();
      this.sucess = 'succesfuly created';
      setTimeout(()  => {
        this.sucess = '';
      }, 700);
    }
  )
    .catch(error => {
     this.err = error;
     setTimeout(()  => {
      this.err = '';
    }, 700);
    });

  }
}
