import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import {FormBuilder, FormGroup , Validators, AbstractControl} from '@angular/forms';
import {  Router } from '@angular/router';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  formgroup: FormGroup ;
  email: AbstractControl;
  password: AbstractControl;
err: any;
loading = false;
  constructor(private service: AuthService , public formbuilder: FormBuilder, private router: Router) {

    this.formgroup = formbuilder.group({
      email: ['', Validators.email],
      password: ['', Validators.required],
});
this.email = this.formgroup.controls['email'];
this.password =  this.formgroup.controls['password'];
   }

  ngOnInit() {
  }
  onSubmit(formgroup) {
    this.loading = true;
   // console.log(formgroup );
 this.service.login(formgroup).then(res => {
if ( res.email === 'admin@admin.com') {
this.err = 'Admin cant login here';
setTimeout(()  => {
  this.err = '';
}, 900);
} else {
console.log(res.user.uid);
localStorage.setItem('uid', res.user.uid);
this.router.navigate(['body/dash']);
}

this.loading = false;
 })
 .catch(err => {
this.err = err;
this.loading = false;
   setTimeout(()  => {
      this.err = '';
    }, 900);
    });


  }
}
