import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup , Validators, AbstractControl} from '@angular/forms'
import { AuthService } from '../auth.service';

@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css']
})
export class AddUserComponent implements OnInit {
  email: AbstractControl;
  password: AbstractControl;
  role: AbstractControl;
course?: AbstractControl;
selected: any;
sucess: any;
err: any;
  formgroup: FormGroup ;
  constructor(public formbuilder: FormBuilder, private service: AuthService) {
    this.formgroup = formbuilder.group({
      email: ['',  Validators.email],
      password: ['',  Validators.required],
      role: ['', Validators.required],
      course: ['']
    });
    this.email = this.formgroup.controls['email'];
    this.password =  this.formgroup.controls['password'];
    this.role =  this.formgroup.controls['role'];
    this.course = this.formgroup.controls['course'];

   }

  ngOnInit() {
  }
  onSubmit(formgroup) {
    return this.service.Register(formgroup.value).then((data)  => { 
      this.formgroup.reset();
      this.sucess = 'succesfuly created';
      setTimeout(()  => {
        this.sucess = '';
      }, 700);
    }
  )
    .catch(error => {
     this.err = error;
     setTimeout(()  => {
      this.err = '';
    }, 700);
    });

  }
  onChange(newValue) {
 this.selected = newValue;
     }

}

