import { Component, OnInit } from '@angular/core';
import 'rxjs/add/operator/switchMap';
import {
  AngularFirestore,
  AngularFirestoreCollection
} from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import { ActivatedRoute, ParamMap } from '@angular/router';
@Component({
  selector: 'app-dash',
  templateUrl: './dash.component.html',
  styleUrls: ['./dash.component.css']
})
export class DashComponent implements OnInit {
  todoCollectionRef: AngularFirestoreCollection<Result>;
  todo$: Observable<Result[]>;
  constructor(private afs: AngularFirestore,  private route: ActivatedRoute) { }

  ngOnInit() {
    this.todoCollectionRef = this.afs.collection<Result>('result', ref => ref.where('lecturer_id', '==', localStorage.getItem('uid')));
    // this.todo$ = this.todoCollectionRef.valueChanges();
    this.todo$ = this.route.paramMap.switchMap(para => {
      return this.todoCollectionRef.valueChanges();

    });
  }

}
export interface Result {
lecturer_id: string;
score: any;
student_email: string;
total: any;
 }